#sidebar-wrapper {
    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        background-color: rgba(0, 0, 0, 0.4);
        transition: opacity 0.3s;
        z-index: $topbar-zindex + 1;
        user-select: none;
        pointer-events: none;

        &.show {
            opacity: 1;
        }
    }

    .sidebar {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: $topbar-zindex + 1;
        transform: translateX(-100%);
        transition: transform 0.3s;

        &.show {
            transform: none;
        }

        .content-overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
        }

        .content {
            position: relative;
            width: 240px;
            max-width: 70%;
            height: 100%;
            background-color: #fff;
            z-index: 2;

            .header {
                display: flex;
                height: 120px;
                background-color: $main-color;
                align-items: center;
                justify-content: center;

                .text {
                    line-height: 1.3;
                    color: #fff;
                    font-size: 20px;
                    text-align: center;
                }
            }

            .nav-item {
                display: flex;
                height: 40px;
                margin: 1px 0 0 0;
                border-left-width: 3px;
                border-left-style: solid;
                background-color: #fff;
                color: $dark2-color;
                font-size: 14px;
                cursor: pointer;
                align-items: center;

                &.active {
                    background-color: #eee;
                }

                &.all {
                    border-left-color: $all-color;
                }
                &.self {
                    border-left-color: $self-color;
                }
                &.work {
                    border-left-color: $work-color;
                }
                &.buy {
                    border-left-color: $buy-color;
                }

                .nav-item-icon {
                    display: flex;
                    width: 40px;
                    height: 40px;
                    align-items: center;
                    justify-content: center;
                    font-size: 16px;
                }
            }
        }
    }
}
