.topbar {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: $topbar-height;
    background-color: $main-color;
    z-index: $topbar-zindex;
    align-items: center;

    .menu-btn {
        display: inline-flex;
        width: $topbar-height;
        height: $topbar-height;
        color: #fff;
        font-size: 22px;
        cursor: pointer;
        align-items: center;
        justify-content: center;
    }

    .menu-title {
        color: #fff;
        font-size: 16px;
    }
}
