* {
    box-sizing: border-box;
}

body {
    font-family: "Open Sans", "Lato", "Helvetica Neue", Helvetica, Arial, "PingFang TC",
        "PingFang SC", "STHeiti", "微軟正黑體", "Microsoft Yahei", sans-serif;
    word-break: break-word;
    margin: 0;
}

a {
    text-decoration: none;
    user-select: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

input,
textarea {
    outline: none;
}

input:focus,
textarea:focus {
    outline: none;
}

button:focus {
    outline: #2d2a2a auto 3px;
}
